import { Component } from '@angular/core';
import { Router } from '@angular/router';
@Component({
  selector: 'app-bar',
  templateUrl: './bar.component.html',
  styleUrls: ['./bar.component.less']
})
export class BarComponent {
  //
  menus: any = [
    {
      "id": 1, "name": "", "childrens": [
        { "id": 10, "name": "", "link": "user" },
        { "id": 11, "name": "", "link": "black" }
      ]
    }, { "id": 2, "name": "", "link": "photo" },
    { "id": 3, "name": "", "link": "order" },
    { "id": 4, "name": "", "link": "statistical" },
    {
      "id": 5, "name": "", "childrens": [
        { "id": 50, "name": "", "link": "filter" },
        { "id": 51, "name": "", "link": "activity" },
        { "id": 52, "name": "", "link": "majia" }
      ]
    },
    {
      "id": 6, "name": "", "childrens": [
        { "id": 60, "name": "", "link": "reportadmin" },
        { "id": 61, "name": "", "link": "reporttype" }
      ]
    }
  ]
  constructor(private router: Router) { }
  child: number;//
  choose;//
  chooseChild;//
  logoutWindow = false;//
  //
  ngOnInit() {
    this.choose = window.location.href.split('content/')[1];
    this.menus.map((item, i) => {
      if (item.childrens) {
        item.childrens.map((child, index) => {
          if (child.link == this.choose) {
            this.choose = item.name;
            this.chooseChild = child.link;
          }
        })
      }

    })
  }
  //
  logoutOk() {
    this.logoutWindow = false;
    localStorage.removeItem("user")
    this.router.navigate(['/login']);
  }
  //
  logoutCancel() {
    this.logoutWindow = false;
  }
  //
  logout() {
    this.logoutWindow = true;
  }
  //
  goLink(e, data) {
    e.stopPropagation()
    localStorage.removeItem("userUuid")
    if (data.childrens) {
      this.showChild(data)
    } else if (data.link) {
      this.router.navigate(['../content/' + data.link]);
      this.choose = data.link;
      this.chooseChild = undefined;
      this.hideChild();
    }
  }
  //
  showChildMenu(e, data) {
    e.stopPropagation()
    if (data.childrens) {
      this.showChild(data)
    }
  }
  //click
  ngAfterViewInit() {
    document.body.addEventListener("click",
      this.hideChild.bind(this));
  }
  //
  goChildLink(e, data, child) {
    e.stopPropagation()
    localStorage.removeItem("userUuid")
    this.choose = data.name;
    this.chooseChild = child.link;
    this.router.navigate(['../content/' + child.link]);
    this.hideChild();
  }
  //
  showChild(data) {
    if (data.childrens) {
      this.child = data.id;
    }
  }
  //
  hideChild() {
    this.child = undefined;
  }
}



